import React from 'react';
import './style.css';
import plural from "plural-ru";
import numberFormat from "../../utils/number-format";

interface BasketSimpleProps {
  user: string;
  totalAmount: number;
  sum: number;
  onOpen: () => void;
}

function BasketSimple(props: BasketSimpleProps) {

  return (
    <>
      {
        props.user && 
        <div className='BasketSimple' data-testid='basket-simple'>
          <span className='BasketSimple-label'> :</span>
          <span className='BasketSimple-total'>{props.totalAmount ? `${props.totalAmount} ${plural(props.totalAmount, '', '', '')}   ${numberFormat(props.sum)} ` : ''}</span>
          <div className='BasketSimple-button' onClick={props.onOpen}/>
        </div>
      }
    </>
  )
}

export default React.memo(BasketSimple);
